.Dchart {
    margin-top: 4%;
    margin-bottom: 4%;
    border: 1px solid #dbe9f5;
    border-radius: 16px;
    box-shadow: 0 4px 6px 0 rgba(31,70,88,.04);
    margin-left: 10%;
    margin-right: 10%;
}

.hchart {
    height: 100;
    width: 300;
}

@media only screen and (max-width: 1050px){
    .Dchart {
        margin-top: 4%;
        margin-bottom: 4%;
        border: 1px solid #dbe9f5;
        border-radius: 16px;
        box-shadow: 0 4px 6px 0 rgba(31,70,88,.04);
        margin-left: 1%;
        margin-right: 1%;
    }
    
}